import React, { lazy, Suspense} from 'react';
import styled from 'styled-components'
import { BrowserRouter, Routes, Route } from "react-router-dom";
import coursesOfFire from './consts/coursesOfFire';

const AppContainer = styled.div`
padding:0 1rem;
max-width:100vw;
`


function App() {

  const Home = lazy(()=>import('./pages/Home'))
  const Setup = lazy(()=>import('./pages/Setup'))
  const COF = lazy(()=>import('./pages/COF'))
  const ScoreDetail = lazy(()=>import('./pages/ScoreDetail'))
  const Marksmanship = lazy(()=>import('./pages/Marksmanship'))
  const History = lazy(()=>import('./pages/History'))


  function getPageDataById(id){
    return coursesOfFire.find((cof)=>cof.id===id)
  }

  return (
    <AppContainer>
    <Suspense fallback = {<div>loading</div>}>
      <BrowserRouter>
        <Routes>
            <Route path="/" element={<Home coursesOfFire = {coursesOfFire} />} />
            <Route path="setup/:cofId" element={<Setup getPageData = {getPageDataById} />} />
            <Route path="scoreDetail/:cofId/:runIndex" element={<ScoreDetail getPageData = {getPageDataById} />} />
            <Route path="marksmanship/:cofId" element={<Marksmanship getPageData = {getPageDataById} />} />
            <Route path="history/:cofId" element={<History getPageData = {getPageDataById}  />} />
            <Route 
              path="cof/:cofId/:sofIndex" 
              element={<COF getPageData = {getPageDataById}/>} />
        </Routes>
      </BrowserRouter>
    </Suspense>
  </AppContainer>

  );
}

export default App;
